import { createGetterSetter } from '../../kibana_utils/public';
export const [getData, setData] = createGetterSetter('Data');
export const [getNotifications, setNotifications] = createGetterSetter('Notifications');
export const [getUISettings, setUISettings] = createGetterSetter('UISettings');
export const [getInjectedMetadata, setInjectedMetadata] = createGetterSetter('InjectedMetadata');
export const [getMapServiceSettings, setMapServiceSettings] = createGetterSetter('MapServiceSettings');
export const [getInjectedVars, setInjectedVars] = createGetterSetter('InjectedVars');
export const getEnableExternalUrls = () => getInjectedVars().enableExternalUrls;
export const [getDocLinks, setDocLinks] = createGetterSetter('docLinks');