import { buildExpression, buildExpressionFunction } from '../../expressions/public';
export const toExpressionAst = vis => {
  const chart = buildExpressionFunction('chart', {
    spec: vis.params.spec
  });
  const ast = buildExpression([chart]);
  return ast.toAst();
};